.Pool-cards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  height: 500px;
  border: 1px solid black;
  overflow: auto;
}

.Pool-card {
  position: relative;
  cursor: pointer;
}

.Pool-card-delete {
  position: absolute;
  top: 0;
  left: 0;
  width: 2rem;
  height: 2rem;
  background-color: rgba(0, 0, 0, 0.8);
  display: none;
}

.Pool-card:hover .Pool-card-delete {
  display: block;
}

.Pool-card-delete::before,
.Pool-card-delete::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1rem;
  height: 0.1rem;
  background-color: #fff;
}

.Pool-card-delete::before {
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
      -ms-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
}

.Pool-card-delete::after {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
      -ms-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
}



